// colors
[data-dark="false"] {
  --primary: #005bac;
  --secondary: #00afec;
  --text: #1d1b20;
  --background: #fefefe;
  --background-alt: #f0f0f0;
  --light-gray: #cac4d0;
  --gray: #79747e;
  --dark-gray: #49454f;
  --overlay: #00000020;
}
[data-dark="true"] {
  --primary: #00afec;
  --secondary: #00afec;
  --text: #e6e0e9;
  --background: #141218;
  --background-alt: #141218;
  --light-gray: #cac4d0;
  --gray: #938f99;
  --dark-gray: #49454f;
  --overlay: #ffffff10;
}

:root {
  // font families
  --title: "Noto Sans", sans-serif;
  --heading: "Noto Sans", sans-serif;
  --body: "Noto Sans", sans-serif;
  --code: "Roboto Mono", monospace;

  // font sizes
  --large: 1.2rem;
  --xl: 1.4rem;
  --xxl: 1.6rem;

  // font weights
  --thin: 200;
  --regular: 400;
  --semi-bold: 500;
  --bold: 600;

  // text line spacing
  --spacing: 2;
  --compact: 1.5;

  // effects
  --rounded: 16px;
  --shadow: 0 0 0px 0 var(--overlay);
  --transition: 0.2s ease;
}
